import React, { useState, useEffect } from 'react';
import api from '../../api/axios';
import {
  Box,
  Container,
  Grid,
  Card,
  CardContent,
  Typography,
  Button,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Alert,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
} from '@mui/material';
import { Check, Speed } from '@mui/icons-material';
import LoadingSpinner from '../common/LoadingSpinner';

const MembershipPlans = () => {
  const [packages, setPackages] = useState([]);
  const [currentPlan, setCurrentPlan] = useState(null);
  const [selectedPlan, setSelectedPlan] = useState(null);
  const [openDialog, setOpenDialog] = useState(false);
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(true);
  const [notification, setNotification] = useState({
    open: false,
    message: '',
    severity: 'success'
  });
  const [paymentMethod, setPaymentMethod] = useState('cash');
  const [upgradeInfo, setUpgradeInfo] = useState(null);

  useEffect(() => {
    fetchData();
  }, []);

  const fetchData = async () => {
    try {
      setLoading(true);
      const [packagesResponse, currentResponse] = await Promise.all([
        api.get('/v1/open/packages'),
        api.get('/v1/subscription/current')
      ]);

      if (packagesResponse.data) {
        setPackages(packagesResponse.data);
      }

      if (currentResponse.data) {
        setCurrentPlan(currentResponse.data);
      }

    } catch (error) {
      console.error('Error fetching data:', error);
      if (error.response?.status !== 404) {
        setError('Khng th ti thng tin gi thnh vin');
      }
    } finally {
      setLoading(false);
    }
  };

  const handleSubscribe = async (plan) => {
    setSelectedPlan(plan);
    setOpenDialog(true);
  };

  const confirmSubscription = async () => {
    if (paymentMethod === 'vnpay') {
      // Chuyn hng n VNPay
      const vnpayUrl = `https://sandbox.vnpayment.vn/paymentv2/vpcpay.html?vnp_TmnCode=4CKTPH2I&vnp_Amount=${selectedPlan.price * 100}&vnp_OrderInfo=Thanh%20toan%20goi%20${selectedPlan.name}&vnp_ReturnUrl=https://yourwebsite.com/vnpay_return&vnp_TxnRef=${Date.now()}`;
      window.location.href = vnpayUrl;
      return;
    }
    try {
      setLoading(true);
      const response = await api.post('/v1/subscription/subscribe', {
        package_id: selectedPlan.id,
        payment_method: paymentMethod
      });
      
      if (response.data.upgrade_info) {
        setUpgradeInfo(response.data.upgrade_info);
      }
      
      setNotification({
        open: true,
        message: response.data.message,
        severity: 'success'
      });
      
      await fetchData();
      setOpenDialog(false);
      
    } catch (error) {
      console.error('Subscription error:', error);
      setNotification({
        open: true,
        message: error.response?.data?.message || 'ng k gi tht bi. Vui lng th li',
        severity: 'error'
      });
    } finally {
      setLoading(false);
    }
  };

  if (loading) return <LoadingSpinner />;

  return (
    <Box sx={{ py: 8, bgcolor: 'background.default' }}>
      <Container maxWidth="lg">
        {notification.open && (
          <Alert 
            severity={notification.severity}
            sx={{ 
              mb: 3,
              maxWidth: 600,
              mx: 'auto',
              boxShadow: 1,
              '& .MuiAlert-message': { flex: 1 }
            }}
            onClose={() => setNotification(prev => ({ ...prev, open: false }))}
          >
            {notification.message}
          </Alert>
        )}

        <Box textAlign="center" mb={8}>
          <Typography 
            variant="h3" 
            component="h1" 
            gutterBottom
            sx={{ 
              fontWeight: 700,
              background: 'linear-gradient(45deg, #2196F3 30%, #21CBF3 90%)',
              WebkitBackgroundClip: 'text',
              WebkitTextFillColor: 'transparent'
            }}
          >
            Gi Thnh Vin
          </Typography>
          <Typography 
            variant="h6" 
            color="text.secondary"
            sx={{ maxWidth: 600, mx: 'auto', mb: 4 }}
          >
            Chn gi ph hp vi nhu cu ca bn
          </Typography>
        </Box>

        {currentPlan && (
          <Alert 
            severity="info" 
            sx={{ 
              mb: 6,
              maxWidth: 600,
              mx: 'auto',
              boxShadow: 1,
              borderRadius: 2,
              '& .MuiAlert-message': { flex: 1 }
            }}
            icon={<Speed />}
          >
            <Typography variant="subtitle1" sx={{ fontWeight: 500 }}>
              Gi hin ti:{' '}
              <Typography component="span" color="primary" fontWeight="bold">
                {currentPlan.package?.name || ''}
              </Typography>
              {currentPlan.end_date && (
                <Typography component="span" color="text.secondary">
                  {' '}- Ht hn: {new Date(currentPlan.end_date).toLocaleDateString('vi-VN')}
                </Typography>
              )}
            </Typography>
          </Alert>
        )}

        <Grid container spacing={4} justifyContent="center">
          {packages && packages.length > 0 ? (
            packages.map((plan) => (
              <Grid item xs={12} md={4} key={plan.id}>
                <Card
                  sx={{
                    height: '100%',
                    display: 'flex',
                    flexDirection: 'column',
                    position: 'relative',
                    borderRadius: 3,
                    boxShadow: 3,
                    transition: 'transform 0.2s ease-in-out, box-shadow 0.2s ease-in-out',
                    '&:hover': {
                      transform: 'translateY(-4px)',
                      boxShadow: 6,
                    },
                  }}
                >
                  <CardContent sx={{ p: 4, flexGrow: 1 }}>
                    <Typography 
                      variant="h5" 
                      gutterBottom
                      sx={{ 
                        fontWeight: 700,
                        color: plan.color || 'primary.main'
                      }}
                    >
                      {plan.name}
                    </Typography>

                    <Box sx={{ my: 4 }}>
                      <Typography
                        variant="h3"
                        component="span"
                        sx={{ 
                          fontWeight: 700,
                          color: plan.color || 'primary.main'
                        }}
                      >
                        {new Intl.NumberFormat('vi-VN').format(plan.price)}
                      </Typography>
                      <Typography
                        variant="subtitle1"
                        component="span"
                        sx={{ color: 'text.secondary', ml: 1 }}
                      >
                        /{plan.duration} thng
                      </Typography>
                    </Box>

                    <List sx={{ mb: 4 }}>
                      <ListItem sx={{ px: 0 }}>
                        <ListItemIcon sx={{ minWidth: 36 }}>
                          <Check sx={{ color: plan.color || 'primary.main' }} />
                        </ListItemIcon>
                        <ListItemText 
                          primary={`Mn ti a ${plan.max_borrows} cun sch`}
                          primaryTypographyProps={{ fontWeight: 500 }}
                        />
                      </ListItem>
                      <ListItem sx={{ px: 0 }}>
                        <ListItemIcon sx={{ minWidth: 36 }}>
                          <Check sx={{ color: plan.color || 'primary.main' }} />
                        </ListItemIcon>
                        <ListItemText 
                          primary={`Thi gian mn ${plan.borrow_duration} ngy`}
                          primaryTypographyProps={{ fontWeight: 500 }}
                        />
                      </ListItem>
                      {plan.can_reserve && (
                        <ListItem sx={{ px: 0 }}>
                          <ListItemIcon sx={{ minWidth: 36 }}>
                            <Check sx={{ color: plan.color || 'primary.main' }} />
                          </ListItemIcon>
                          <ListItemText 
                            primary="t trc sch"
                            primaryTypographyProps={{ fontWeight: 500 }}
                          />
                        </ListItem>
                      )}
                    </List>

                    <Button
                      variant="contained"
                      size="large"
                      fullWidth
                      onClick={() => handleSubscribe(plan)}
                      sx={{
                        py: 1.5,
                        bgcolor: plan.color || 'primary.main',
                        '&:hover': {
                          bgcolor: plan.color ? `${plan.color}CC` : 'primary.dark',
                        },
                        borderRadius: 2,
                        boxShadow: 2,
                      }}
                    >
                      ng k ngay
                    </Button>
                  </CardContent>
                </Card>
              </Grid>
            ))
          ) : (
            <Typography variant="h6" color="text.secondary" textAlign="center">
              Khng c gi thnh vin no
            </Typography>
          )}
        </Grid>

        <Dialog open={openDialog} onClose={() => setOpenDialog(false)}>
          <DialogTitle>Xc nhn ng k gi</DialogTitle>
          <DialogContent>
            {error && <Alert severity="error" sx={{ mb: 2 }}>{error}</Alert>}
            <Typography gutterBottom>
              Bn c chc chn mun ng k {selectedPlan?.name || ''} vi gi{' '}
              {new Intl.NumberFormat('vi-VN').format(selectedPlan?.price)}/{selectedPlan?.duration} thng?
            </Typography>
            
            {upgradeInfo && (
              <Box sx={{ mt: 2 }}>
                <Typography variant="subtitle1" color="text.secondary">
                  Thng tin nng cp t gi {upgradeInfo.previous_package}:
                </Typography>
                <Typography>
                  Gi tr cn li: {new Intl.NumberFormat('vi-VN').format(upgradeInfo.remaining_value)}
                </Typography>
                <Typography>
                  Ph nng cp: {new Intl.NumberFormat('vi-VN').format(upgradeInfo.upgrade_fee)}
                </Typography>
              </Box>
            )}

            <FormControl fullWidth sx={{ mt: 2 }}>
              <InputLabel>Phng thc thanh ton</InputLabel>
              <Select
                value={paymentMethod}
                onChange={(e) => setPaymentMethod(e.target.value)}
              >
                <MenuItem value="cash">Tin mt</MenuItem>
                <MenuItem value="transfer">Chuyn khon</MenuItem>
                <MenuItem value="card">Th tn dng</MenuItem>
                <MenuItem value="card">Thanh ton VNPAY</MenuItem>
              </Select>
            </FormControl>
          </DialogContent>
          <DialogActions>
            <Button onClick={() => setOpenDialog(false)}>Hy</Button>
            <Button onClick={confirmSubscription} variant="contained" color="primary">
              Xc nhn
            </Button>
          </DialogActions>
        </Dialog>
      </Container>
    </Box>
  );
};

export default MembershipPlans; 